import { directive } from '@babel/types';
import React from 'react';

class Home extends React.Component{
    render(){
        return(
            
        <div>
		<nav id="navbar">
		<div id="logo">
	<img src="assets/css/art.png" alt="myartgallery.com"/>
	</div>
	<ul>
		<li class="item"><a href="#home">Home</a></li>
		<li class="item"><a href="#services-container">Services</a></li>
		<li class="item"><a href="#client-section">Our Clients</a></li>
		<li class="contact"><a href="#contact">Contact us</a></li>
	</ul>



</nav>

<section id="home">
	<h1 class="h-primary">Welcome to My art gallery</h1>
	<p>
		Mandala Stalker
	</p>
	<p>
		Purwar
	</p>
	<button class="btn"><a href="https://www.instagram.com/mandala_stalkers/?hl=en">Order now</a></button>
	
</section>
<section id="services-container">
	<h1 class="h-primary center">Our Services</h1>
	<div id="services">
		<div class="box">
			<img src="assets/css/picture 1.png" alt=""/>
			<h2 class="h-secondary center">painting order</h2>
			<p class="center"> <a href="https://www.instagram.com/mandala_stalkers/?hl=en">Mandala Stalker</a> </p>
		</div>
		<div class="box">
			<img src="assets/css/picture 2.png" alt=""/>
			<h2 class="h-secondary center">painting order</h2>
			<p class="center"> <a href="https://www.instagram.com/mandala_stalkers/?hl=en">Mandala Stalker</a></p>
		</div>
		<div class="box">
			<img src="assets/css/picture 3.png" alt=""/>
			<h2 class="h-secondary center">painting order</h2>
			<p class="center"><a href="https://www.instagram.com/mandala_stalkers/?hl=en">Mandala Stalker</a> </p>
		</div>
		<div class="box">
			<img src="assets/css/picture 4.png" alt=""/>
			<h2 class="h-secondary center">painting order</h2>
			<p class="center"> <a href="https://www.instagram.com/mandala_stalkers/?hl=en">Mandala Stalker</a></p>
		</div>
	</div>
</section>

<section id="client-section">
	<h1 class="h-primary center">Our Artist</h1>
	<div id="clients">
		<div class="client item">
			<img src="assets/css/picture 4.png" alt="Our Artist"/>
		</div>
	</div>
</section>
<section id="contact">
	<h1 class="h-primary center">Contact Us</h1>
	<div id="contact-box">
		<form action="">
			<div class="form-group">
				<label for="name">Name: </label>
				<input type="text" name="name" id="name" placeholder="Enter your name"/>
			</div>
			<div class="form-group">
				<label for="email">Email: </label>
				<input type="email" name="name" id="email" placeholder="Enter your email"/>
			</div>
			<div class="form-group">
				<label for="phone">Phone nuber: </label>
				<input type="phone" name="name" id="phone" placeholder="Enter your mobile number"/>
			</div>
			<div class="form-group">
				<label for="message">Message: </label>
				<textarea name="message" id="message" cols="30" rows="10"></textarea>			</div>
				<button class="submit">submit</button>
		</form>
		
	</div>
	
</section>
<footer>
	<div class="center">
		Copyright &copy; www.com All rights reserved!
	</div>
</footer>

        </div>
        
         ); }
}

export default Home;